.UpperHeader2{ 
    object-position: top center;
    object-fit: cover;
    max-height: 100%;
    min-height: 8vh;

}

.DetailPanel {
    text-align: center;
    color:#fff;
    min-height: 100%;
    background:linear-gradient(-90deg, rgba(255,0,0,0), rgba(0,0,0,0.8)15%);
    padding: 2% 10% 2% 4%;   
    
}

.DetailPanel img{
    border-radius: 15px;
}

.player {
    margin-top: 5%;
    margin-left:5%
}

.cast :hover{ 
    color:black;
    text-decoration: none;
    -webkit-transform: translateY(-3px);
}